<template>
<transition name="slide-top-menu">
  <div class="slidemenu" v-show="visible">
    <div class="slidemenu-conent">
      <div class="login-header">
        <div class="top-back" @click="goBack">
          <span class="iconfont icon-return-arrow"></span>
        </div>
        <div class="center">
          风险评估
        </div>
      </div>
      <!-- 试卷信息 -->
      <div class="contentInfo" v-show="infoVisible">
          <mt-cell title="考试时间" :value="testTime"></mt-cell>
          <mt-cell title="考试名称" :value="riskName"></mt-cell>
          <mt-button type="danger" @click="start" size="large" class="">开始答题</mt-button>
      </div>


      <!-- 试题 -->
      <div class="contentQuestion" v-show="questionVisible">
        <ul>
          <li v-for="(item,index) in question">
            <template v-if="item.questionType == 0"> <!-- 单选题 -->
              <mt-radio
                :title=item.questionContext
                :index="index"
                v-model="item.value"
                :options="item.options"
              >
              </mt-radio>
            </template>
            <template v-else-if="item.questionType == '1'"> <!-- 多选题 -->
              <mt-checklist
                :title=item.questionContext
                :index="index"
                v-model="item.value"
                :options="item.options"
              >
              </mt-checklist>
            </template>
            <template v-else-if="item.questionType == '2'"> <!-- 判断题 -->
              <mt-radio
                :title=item.questionContext
                :index="index"
                v-model="item.value"
                :options="item.options"
              >
              </mt-radio>
            </template>
            <template v-else> <!-- 填空题 -->
              <!-- <mt-field label="自我介绍" placeholder="自我介绍" type="textarea" row="4" v-model="introduction"></mt-field> -->
              <div class="inputTest">
                <div class="inputLabel">
                  {{ item.questionContext }}
                </div>
                <div class="inputInput">
                  <textarea name="name"  v-model=InputValue>

                  </textarea>
                </div>
              </div>
            </template>
          </li>
        </ul>
        <mt-button type="danger" @click="handOn" size="large" class="">交卷</mt-button>
      </div>
    </div>

    <!-- <mt-checklist
      :title="123"
      :index="1"
      v-model="value"
      :options="['1','2','3']"
    >
    </mt-checklist> -->

  </div>
</transition>
</template>

<script>
import {
  Field,
  Radio,
  Checklist,
  Button,
  MessageBox
} from 'mint-ui';
import cookieUtil from 'js-cookie'
Vue.component(Button.name, Button);
Vue.component(Radio.name, Radio);
Vue.component(Checklist.name, Checklist);
Vue.component(Field.name, Field);
export default {
  name: 'slidemenu',
  data() {
    return {
    
      visible: true,
      infoVisible:true,
      questionVisible:false,
      testTime:'',
      riskName:'',
      InputValue:'',
      introduction:'',
      //测试编号信息
      paperNo:'',
      testSno:'',
      suitableNo:'',
      suitableName:'',
      //试题
      option:[],
      question:[],
      // options:[
      //   {"questionNo":1001,"optionNo":1,"optionContext":"A．30岁以下；","correctFlag":"2","optionRemark":"3","updDate":1511366400000},
      //   {"questionNo":1001,"optionNo":2,"optionContext":"B．31-40岁；","correctFlag":"2","optionRemark":"7","updDate":1511366400000},
      //   {"questionNo":1001,"optionNo":3,"optionContext":"C．41-50岁；","correctFlag":"2","optionRemark":"9","updDate":1511366400000},
      //   {"questionNo":1001,"optionNo":4,"optionContext":"D．51-60岁；","correctFlag":"2","optionRemark":"5","updDate":1511366400000},
      //   {"questionNo":1001,"optionNo":5,"optionContext":"E．60岁以上。","correctFlag":"2","optionRemark":"1","updDate":1511366400000}
      // ],
      // questions: [
      //   {"questionNo":1001,"questionCls":"0","questionType":"1","questionContext":"问您的年龄处于：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1002,"questionCls":"0","questionType":"2","questionContext":"家庭预计进行证券投资的资金占家庭现有总资产(不含自住、自用房产及汽车等固定资产)的比例是：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1003,"questionCls":"0","questionType":"1","questionContext":"行一项重大投资后，您通常会觉得：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1004,"questionCls":"0","questionType":"1","questionContext":"果您需要把大量现金整天携带在身的话，您是否会感到：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1005,"questionCls":"0","questionType":"0","questionContext":"您独自到外地游玩，遇到三岔路口，您会选择：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1006,"questionCls":"0","questionType":"0","questionContext":"设有两种不同的投资：投资A预期获得5%的收益，有可能承担非常小的损失；投资B预期获得20%的收益，但有可能面临25%甚至更高的亏损。您将您的投资资产分配为：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1007,"questionCls":"0","questionType":"2","questionContext":"如您前期用25元购入一只股票，该股现在升到30元，而根据预测该股近期有一半机会升到35元，另一半机会跌到25元，您现在会：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1008,"questionCls":"0","questionType":"0","questionContext":"上题情况，该股现在已经跌到20元，而您估计该股近期有一半机会升回25元，另一半机会继续下跌到15元，您现在会：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1009,"questionCls":"0","questionType":"","questionContext":"您进行投资时，您的首要目标是：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1010,"questionCls":"0","questionType":"0","questionContext":"您的投资经验可以被概括为：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1011,"questionCls":"0","questionType":"0","questionContext":"您是否了解证券市场的相关知识：","questionRemark":" ","updDate":1511366400000},
      //   {"questionNo":1012,"questionCls":"0","questionType":"4","questionContext":"您用于证券投资的资金不会用作其它用途的时间段为：","questionRemark":" ","updDate":1511366400000}
      // ]
    }
  },
  computed: {
    // this.option = this.options

  },
  created: function() {
    // axiosUtil.suitableQry().then(function(data){
    //   this.suit = data;
    // })
    var self = this;
    axiosUtil.paperInit().then(function(data){
      var data = data.data.data;
      self.testTime = data.testPaper.testMinutes?data.testPaper.testMinutes+"分钟":'无时间限制';
      self.riskName = data.testPaper.paperName;
      self.suitableNo = data.suitableNo;
      self.suitableName = data.suitableName;
      self.paperNo = data.paperNo
      self.testSno = data.testSno
    });

  },
  watch: {
    value(val) {
      this.visible = val.isShow;
    },
    visible(val) {
      this.value.isShow = true;
    }
  },
  methods: {
    goBack: function() {
      this.$router.go(-2);
    },
    start:function(){
      MessageBox.confirm("是否开始答题？").then(action => {
        this.infoVisible = false
        this.questionVisible = true;
        var self = this;
        axiosUtil.paperApp({paperNo:this.paperNo, testSno:this.testSno, suitableNo:this.suitableNo, suitableName:this.suitableName}).then(function(data){
          var data = data.data.data.questions;
          for(var i = 0;i<data.length;i++){
            if(data[i].questionType == 1){
              data[i].value = []
            }else{
              data[i].value = ''
            }

            for(var j = 0;j<data[i].options.length;j++){
              data[i].options[j].label=data[i].options[j].optionContext
              data[i].options[j].value=data[i].options[j].optionNo
            }
          }
          self.question = data;

        })

      })


    },
    handOn: function(){
      var flag = false; //判断有没有题没有答
      var questionData = []
      for(var i = 0;i<this.question.length;i++){
        questionData.push({"questionNo":this.question[i].questionNo,"optionNos":typeof(this.question[i].value)=='object'?this.question[i].value.toString():this.question[i].value})

        if(!this.question[i].value){
          flag = true
        }
      }


      let message = flag?"你还有题没有完成！是否提交？":"是否确认提交？";
      MessageBox.confirm(message).then(action => {
        console.log(questionData)
        var self = this;
        axiosUtil.paperEnd({paperNo:this.paperNo, testSno:this.testSno,answers:JSON.stringify(questionData)}).then(function(data){
          var data = data.data
          if(data.errorcode == -1){
            MessageBox("提示", data.errormsg);
          }else{

            MessageBox.alert(data.data).then(function(){
              self.$router.push("/index")
            })
          }

        })
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#app{
  overflow: scroll ! important;
  height: auto ! important;
}
.slidemenu {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, .3);
  transition: .5s all ease;
}

.slidemenu-conent {
  /*height: 100%;*/
  width: 100%;
  background: #fff;
}

.login-header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  height: 45px;
  line-height: 45px;
  font-size: 19px;
  background: #ef4f4f;
  color: #ffffff;
  text-align: center;
}

.top-back {
  max-width: 85px;
  float: left;
}

.contentQuestion,.contentInfo{
  margin-top: 45px;
  overflow: scroll;
  height: 167vmin;
}
.app-footer{
  position: fixed ! important;
}
.inputLabel{
  font-size: 18px;
  margin: 8px;
  display: block;
  color: #888;
}
.inputInput textarea{
  width: 97%;
  margin-left: 3%;
  font-size: 18px;
}

</style>



